@mixin flex-kit($jcontent) {
    display: flex;
    justify-content: $jcontent;
    align-items:center;
  }
  
  
  @mixin font-value($weight,$size,$height,$t-transform:none){
   font-weight: $weight;
   font-size: $size;
   line-height: $height;
   text-transform: $t-transform;
  }
  
  @mixin bg-img {
    background-size:cover;
    background-position:center;
    background-repeat: no-repeat;
  }