@font-face {
  font-family: "Montserrat", sans-serif;
  src: url("../fonts/Montserrat-Regular.ttf");
  src: url("../fonts/Montserrat-Bold.ttf");
  src: url("../fonts/Montserrat-Thin.ttf");
}
@font-face {
  font-family: "Lato", sans-serif;
  src: url("../fonts/Lato-Regular.ttf");
  src: url("../fonts/Lato-BoldItalic.ttf");
}
.btn {
  padding: 10px 20px;
  text-align: center;
  cursor: pointer;
  background-color: #8D81AC;
  border-radius: 40px;
  font-weight: 700;
  font-size: 21px;
  line-height: 21px;
  text-transform: none;
  color: #ffffff;
  transition: transform 0.6s;
  border: 2px solid #6a41d1;
}
@media (max-width: 480px) {
  .btn .card_btn {
    max-width: 100px;
    text-align: center;
    font-size: 18px;
    padding: 15px 29px;
  }
}
.btn:hover {
  background-color: white;
  color: #8D81AC;
}
.btn:active {
  transform: scale(0.8);
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: sans-serif;
  background-color: #8D81AC;
}

a {
  text-decoration: none;
}

ul {
  padding: 0;
  margin: 0;
}

li {
  list-style-type: none;
}

h1, h2, h3, h4, h5, p {
  margin: 0;
}

.container {
  width: 1200px;
  margin: 0 auto;
  padding: 0 10px;
  align-self: center;
}
@media (max-width: 1200px) {
  .container {
    width: 100%;
    padding: 0;
  }
}

.hide {
  display: none;
}

.header {
  width: 100%;
  min-height: 100px;
  border-bottom: 2px solid white;
}
.header_container {
  margin: 0 auto;
  width: 80%;
  height: inherit;
}
.header_title {
  font-family: "Montserrat", sans-serif;
  font-style: italic;
  color: white;
  font-weight: bold;
  font-size: 40px;
  line-height: 100px;
  text-transform: none;
}

.cards_container {
  width: 100%;
  margin: auto, 0;
  padding: 40px 0 40px 0;
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-wrap: wrap;
}
.cards_container img {
  width: 60px;
  height: 60px;
}

.card {
  width: 350px;
  min-height: 300px;
  padding: 10px;
  border: 2px solid white;
  border-radius: 10px;
  margin-bottom: 40px;
}
.card_header {
  width: 100%;
  height: 80px;
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-direction: column;
  border-bottom: 2px dotted white;
}
.card_title {
  font-family: "Lato", serif;
  color: #a5d360;
  font-size: 25px;
}
.card_data {
  color: rgb(155, 49, 49);
  font-weight: bold;
  text-decoration: underline;
  font-size: 20px;
}
.card_organizer-wraper {
  font-size: 18px;
  height: 40px;
  color: white;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.card_organizator {
  font-style: italic;
  font-weight: normal;
}
.card_organizator_description {
  font-weight: normal;
}
.card_description-wrapper {
  padding: 10px;
}
.card_footer {
  height: 40px;
  padding: 0 30px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.card_footer a {
  display: block;
  color: white;
  font-weight: bold;
  font-size: 18px;
  line-height: 0;
  text-transform: uppercase;
}
.card_footer a:hover {
  color: #a5d360;
}
.card_email {
  color: white;
  text-align: center;
  margin-top: 10px;
  font-weight: 300px;
  font-size: 20px;
  line-height: 30px;
  text-transform: none;
}

.--participants {
  min-height: 50px;
  background-color: rgb(58, 133, 168);
}

.registration {
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-direction: column;
  padding: 30px 0;
}
.registration_input {
  margin-bottom: 15px;
  width: 340px;
  height: 20px;
  padding: 15px 19px;
  border-radius: 4px;
  border: 1px solid #f2f2f2;
  transition: box-shadow 0.3s ease;
  font-weight: 300px;
  font-size: 20px;
  line-height: 30px;
  text-transform: none;
}
.registration_input:focus {
  outline: none;
  box-shadow: 0 3px 8px rgba(0, 0, 0, 0.21);
}
.registration_conditions {
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-direction: column;
}
.registration_conditions_question {
  font-weight: 300px;
  font-size: 23px;
  line-height: 30px;
  text-transform: none;
  color: white;
}
.registration_conditions_checkboxes {
  padding: 20px;
}
.registration_conditions label {
  color: white;
}
.registration_conditions label + input {
  margin-left: 10px;
}
.registration_btn {
  margin-top: 10px;
}

.footer {
  width: 100%;
  min-height: 100px;
  border-top: 2px solid white;
  display: flex;
  justify-content: center;
  align-items: center;
}

.pagination {
  width: 340px;
  display: flex;
  justify-content: space-around;
  align-items: center;
  font-weight: 600px;
  font-size: 20px;
  line-height: 0px;
  text-transform: none;
}
.pagination_numbers-wrapper {
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.pagination_number + .pagination_number {
  margin-left: 30px;
}
.pagination a {
  color: white;
}
.pagination a:hover {
  color: #a5d360;
}